import {
    Component,
    OnInit,
    Input,
    ContentChild,
    HostBinding,
    OnDestroy,
    ViewChild,
    Optional,
    NgZone,
    Injector,
    ElementRef,
    Renderer2,
} from "@angular/core";
import { FarrisSectionTitleDirective } from "./directive/section-title.directive";
import { FarrisSectionHeaderDirective } from "./directive/section-header.directive";
import { FarrisSectionHcontentDirective } from "./directive/section-hcontent.directive";
import {
    FarrisSectionToolbarDirective,
    FarrisSectionViewChangeDirective,
} from "./directive/section-toolbar.directive";
import { FarrisSectionExtendDirective } from "./directive/section-extend.directive";
import { FarrisSectionContentDirective } from "./directive/section-content.directive";
import { Observable, Subject, Subscription, of } from "rxjs";
import { ToolbarConfig, ButtonConfig } from "./toolbar.config";
import ResizeObserver from "resize-observer-polyfill";
import { map, debounceTime } from "rxjs/operators";
import { ComponentTemplateRef } from "@farris/ui-common";
@Component({
    selector: "farris-section",
    templateUrl: "./section.component.html",
    styleUrls: ["./section.component.css"],
})
export class FarrisSectionComponent implements OnInit, OnDestroy {
    @Input() contentCls = "";
    @HostBinding("class.f-section") baseCls = true;
    @HostBinding("class.f-section-maximize") maxStatus: boolean = false;
    @HostBinding("class.f-section-fill") get fillCls(): boolean {
        return this.fill;
    }
    @HostBinding("class.f-section-accordion")
    get enableAccordionCls(): boolean {
        return this.enableAccordion == "default";
    }
    @HostBinding("class.f-state-collapse") get enableCollapseCls(): boolean {
        if (
            this.enableAccordion === "default" ||
            this.enableAccordion === "custom"
        ) {
            return !this.expandStatus;
        } else {
            return false;
        }
    }

    @HostBinding("class.f-section-custom-accordion")
    get enableCustomAccordionCls(): boolean {
        return this.enableAccordion == "custom";
    }
    // ä¸»æ é¢
    @Input() mainTitle: string;
    // å¯æ é¢
    @Input() subTitle: string;
    // æ¯å¦æ¾ç¤ºheader
    @Input() showHeader = true;
    // æ¯å¦æ¯ææå¤§å
    @Input() enableMaximize = false;
    /**
     * æ¯å¦å¯ç¨åæåè½
     * é»è®¤ä¸å¯ç¨åæ,å¼ä¸º''
     * å¯ç¨é»è®¤åæ,å¼ä¸º'default'
     * å¯ç¨èªå®ä¹åæ,å¼ä¸º 'custom'
     **/
    @Input() enableAccordion = "";
    // sectionæ¯å¦éºæ»¡
    @Input() fill = false;
    // é»è®¤æ¯æ¯å¦å±å¼
    @Input() expandStatus = true;
    /**
     * äºä»¶ä¸ä¸æï¼è§£å³æé®ç¹å»æ¶æ²¡æä¸ä¸æçé®é¢
     */
    @Input() context: any;
    @Input() index: any;
    // å°±æ¥æé®ä½ç½®
    toolbarPosition = "";
    //
    toolbarBtns = [];
    // å·¥å·æ æé®
    private _toolbar: ToolbarConfig | null = null;
    @Input()
    set toolbar(value: ToolbarConfig | null) {
        if (value) {
            this._toolbar = value;
            // è®¡ç®ä½ç½®
            this.toolbarPosition = this.getToolbarPosition();
            //
            this.toolbarBtns = [...this.toolbar.contents];
            this.inMoreButtonContents = [];
            // ç»å®äºä»¶
            this.bindResizeEvent();
            // è°æ´çé¢
            this.afterBindToolbarData();
        } else {
            this._toolbar = null;
            // æä½ç½®
            this.toolbarPosition = "";
            this.toolbarBtns = [];
            this.inMoreButtonContents = [];
            // ç§»é¤ç»å®äºä»¶
            this.unBindResizeEvent();
        }
    }
    get toolbar() {
        return this._toolbar;
    }
    //æ è®°æé®å·¥å·æ æ¯å¦éè[æ´å¤]æé®
    toolbarDpHidden = true;
    @Input() ctx;
    // sectionå¤´é¨
    @Input() headerComponentType: ComponentTemplateRef;
     // sectionå¤´é¨æ é¢åºæ¨¡æ¿
    @Input() titleComponentType: ComponentTemplateRef;
    //sectionå¤´é¨å¯æ©å±åºåæ¨¡æ¿
    @Input() contentComponentType: ComponentTemplateRef;
     //sectionå¤´é¨å·¥å·æ åºåæ¨¡æ¿
    @Input() toolbarComponentType: ComponentTemplateRef;
    //sectionå¯æ©å±åºåæ¨¡æ¿ââåå®¹åºä¸é¢çæ©å±ï¼æ é¢ä¸é¢
    @Input() extendComponentType: ComponentTemplateRef;
    //sectionå¤´é¨ViewChangeåºåæ¨¡æ¿
    @Input() viewchagneComponentType: ComponentTemplateRef;

    // sectionå¤´é¨æ¨¡æ¿
    @ContentChild(FarrisSectionHeaderDirective) headerDirective: any;
    // sectionå¤´é¨æ é¢åºæ¨¡æ¿
    @ContentChild(FarrisSectionTitleDirective) headerTitleDirective: any;
    //sectionå¤´é¨å¯æ©å±åºåæ¨¡æ¿
    @ContentChild(FarrisSectionHcontentDirective) headerContentDirective: any;
    //sectionå¤´é¨å·¥å·æ åºåæ¨¡æ¿
    @ContentChild(FarrisSectionToolbarDirective) headerToolbarDirective: any;
    //sectionå¯æ©å±åºåæ¨¡æ¿ââåå®¹åºä¸é¢çæ©å±ï¼æ é¢ä¸é¢
    @ContentChild(FarrisSectionExtendDirective) extendDirective: any;
    //sectionåå®¹åºåæ¨¡æ¿
    @ContentChild(FarrisSectionContentDirective) contentDirective: any;
    //sectionå¤´é¨ViewChangeåºåæ¨¡æ¿
    @ContentChild(FarrisSectionViewChangeDirective) viewChangeDirective: any;

    // åç§Tabä¸­è¿æ ·çå¤çæ¹å¼æé®é¢----å»¶åä¿®æ¹
    // æ¯å¦è¢«ç¦ç¨çç¶æ
    // æç§ä¹åçé»è¾ï¼å¦ææ²¡ææ¾å°keyï¼å°±è¯´æè¢«ç¦ç¨
    _disableDatas: Observable<Array<{ [key: string]: boolean }>> =
        new Subject();
    @Input()
    set btnStates(values: Observable<Array<{ [key: string]: boolean }>>) {
        if (values) {
            if (
                this._disableDatas &&
                this._disableDatas.hasOwnProperty("unsubscribe")
            ) {
                this._disableDatas["unsubscribe"]();
            }
            this._disableDatas = values;
        }
    }
    // æç§ä¹åçé»è¾ï¼å¦ææ²¡ææ¾å°keyï¼å°±è¯´æå¯è§
    _visibleDatas: Observable<Array<{ [key: string]: boolean }>> =
        new Subject();
    // æ¯å¦å¯è§çç¶æ
    @Input()
    set btnVisible(values: Observable<Array<{ [key: string]: boolean }>>) {
        if (values) {
            if (
                this._visibleDatas &&
                this._visibleDatas.hasOwnProperty("unsubscribe")
            ) {
                this._visibleDatas["unsubscribe"]();
            }
            this._visibleDatas = values;
        }
    }
    @Input() clickThrottleTime = 350;

    // éè¿æé®æ°ç»æ¾ç¤ºå·¥å·æ 
    @ViewChild("toolbarContainer") toolbarContainer;
    // è®¡ç®å·¥å·æ æé®å®½åº¦çè¾å©åºå
    @ViewChild("btnPlaceholder") btnPlaceHolder;

    private clickItems = new Subject<any>();
    private clickSubscription: Subscription;
    // ç¨äºæä¾å¾ªç¯æç
    trackByButton(index: number, btn: any): number {
        return btn.id;
    }
    // ç¨äºè®¡ç®ååºæ¶
    private ro: ResizeObserver = null;
    private resizeEl = null;
    // å¤çåå
    private ngZone;
    //
    private renderer;
    inMoreButtonContents: ButtonConfig[] = [];

    constructor(@Optional() private inject: Injector) {
        if (this.inject) {
            this.ngZone = this.inject.get(NgZone, null);
            this.renderer = this.inject.get(Renderer2, null);
        }
    }

    ngOnInit() {
        // æ è®°ç¹å»äºä»¶
        this.clickSubscription = this.clickItems
            .pipe(debounceTime(this.clickThrottleTime))
            .subscribe((btnInfo: ButtonConfig) => {
                if (btnInfo.click && typeof btnInfo.click == "function") {
                    btnInfo.click({ context: this.context });
                }
            });
        // ç»å®ç¶æ
        this._disableDatas.subscribe((data) => {
            // è°æ´æé®å¯ç¨ç¶æ
            this.changeState(data, "disable");
        });
        this._visibleDatas.subscribe((data) => {
            // è°æ´æé®å¯è§ç¶æ
            this.changeState(data, "visible");
        });
    }
    /**
     * è·åæé®éç½®ä¸­ï¼ç¦ç¨ãå¯è§çå±æ§
     * @param id
     * @param stateProp
     */
    getState(btnItem, stateProp) {
        if (btnItem.hasOwnProperty(stateProp)) {
            return btnItem[stateProp];
        }
        // ä¸å­å¨å±æ§
        if (stateProp == "visible") {
            return true;
        }
        return false;
    }
    /**
     * æ¹åå¯ç¨ç¶æ
     * @param values
     */
    private changeState(values, stateProp) {
        const idArray = Object.keys(values);
        idArray.forEach((id) => {
            const state = values[id];
            const item = this._findItemByID(id, this.toolbarBtns);
            if (item) {
                item[stateProp] = state;
            }
        });
        this.inMoreButtonContents.forEach((item) => {
            const findId = idArray.find((id) => id == item.id);
            if (findId) {
                item[stateProp] = values[findId];
            }
        });
    }

    /**
     * æ¥æ¾
     * @param id
     * @param datas
     */
    private _findItemByID(id: string, datas: ButtonConfig[]) {
        return datas.find((item) => item.id == id);
    }
    ngOnDestroy() {
        if (this.clickSubscription) {
            this.clickSubscription.unsubscribe();
            this.clickSubscription = null;
        }
        if (
            this._visibleDatas &&
            this._visibleDatas.hasOwnProperty("unsubscribe")
        ) {
            this._visibleDatas["unsubscribe"]();
            this._visibleDatas = null;
        }
        if (
            this._disableDatas &&
            this._disableDatas.hasOwnProperty("unsubscribe")
        ) {
            this._disableDatas["unsubscribe"]();
            this._disableDatas = null;
        }
        this.unBindResizeEvent();
    }
    /**
     * ç»å®éå¶
     */
    private bindResizeEvent() {
        // æ è®°ååºæ¶
        if (this.ngZone && this.toolbar && this.toolbar.contents.length > 0) {
            this.ngZone.runOutsideAngular(() => {
                setTimeout(() => {
                    if (!this.toolbarContainer) {
                        return;
                    }
                    if (this.ro) {
                        if (
                            this.toolbarContainer.nativeElement.parentElement ==
                            this.resizeEl
                        ) {
                            // å·²ç»å®è¿
                            return;
                        } else {
                            this.unBindResizeEvent();
                        }
                    }
                    if (this.ro === null) {
                        this.ro = new ResizeObserver((el) => {
                            this._calculateToolbarDropdownIsShow();
                            this._calculateButtonsShow();
                        });
                        this.resizeEl =
                            this.toolbarContainer.nativeElement.parentElement;
                        this.ro.observe(this.resizeEl);
                    }
                }, 0);
            });
        } else {
            this.unBindResizeEvent();
        }
    }
    /**
     * æé®æ°æ®åæ´åè§¦åæ£æ¥
     */
    private afterBindToolbarData() {
        if (this.ngZone && this.toolbar && this.toolbar.contents.length > 0) {
            this.ngZone.runOutsideAngular(() => {
                setTimeout(() => {
                    this._calculateToolbarDropdownIsShow();
                    this._calculateButtonsShow();
                }, 0);
            });
        }
    }
    /**
     * å¤æ­ä¸ææé®æ¯å¦æ¾ç¤º
     */
    private _calculateToolbarDropdownIsShow() {
        if (!this.toolbarContainer || !this.toolbar || !this.toolbarPosition) {
            // å®¹å¨æªæ¾ç¤ºï¼æ²¡ææ°æ®ï¼æ²¡ææé®ä½ç½®
            this.toolbarDpHidden = true;
            return;
        }
        const btnParentsContainer =
            this.toolbarContainer.nativeElement.parentElement;
        if (
            this.btnPlaceHolder.nativeElement.scrollWidth >
            btnParentsContainer.offsetWidth
        ) {
            this.toolbarDpHidden = false;
        } else {
            this.toolbarDpHidden = true;
        }
    }
    /**
     * è®¡ç®æ¾ç¤ºçæé®ä¸ªæ°åæ´å¤ä¸­æ¾ç¤ºçæé®ä¸ªæ°
     */
    private _calculateButtonsShow() {
        if (!this.toolbarContainer || !this.toolbar || !this.toolbarPosition) {
            // å®¹å¨æªæ¾ç¤ºï¼æ²¡ææ°æ®ï¼æ²¡ææé®ä½ç½®
            return;
        }
        if (this.toolbarDpHidden) {
            this.toolbarBtns = [...this.toolbar.contents];
            this.inMoreButtonContents = [];
            return;
        }
        let toolbarParentContainerEl =
            this.toolbarContainer.nativeElement.parentElement;
        let divEl = this.toolbarContainer.nativeElement;
        let btnPlaceHolderEl = this.btnPlaceHolder.nativeElement;
        let btnPlaceHolderBtns = btnPlaceHolderEl.querySelectorAll("button");

        // æ´å¤æé®
        const moreButton = toolbarParentContainerEl.querySelector(".morebtn");
        // è®¡ç®æé®çå®½åº¦
        let basicStart = parseInt(
            btnPlaceHolderEl.getBoundingClientRect().left
        );
        let basicWidth =
            toolbarParentContainerEl.offsetWidth - moreButton.offsetWidth - 12;
        let toolbarDropdownHidden = true;
        let k = 0;
        // åªæä¸ä¸ªæé®
        if (btnPlaceHolderBtns.length == 1) {
            if (btnPlaceHolderEl.offsetWidth > basicWidth) {
                toolbarDropdownHidden = false;
            } else {
                k = 1;
            }
        } else {
            for (k = 1; k <= btnPlaceHolderBtns.length - 1; k++) {
                // å¦æåå®¹è¶åº,èèå°æé®ä¹é´çé´è·
                if (k == btnPlaceHolderBtns.length - 1) {
                    if (btnPlaceHolderEl.offsetWidth > basicWidth) {
                        toolbarDropdownHidden = false;
                        k = k - 1;
                        break;
                    }
                } else if (
                    parseInt(
                        btnPlaceHolderBtns[k].getBoundingClientRect().left
                    ) -
                        basicStart >
                    basicWidth
                ) {
                    toolbarDropdownHidden = false;
                    k = k - 1;
                    break;
                }
            }
        }

        this.toolbarDpHidden = toolbarDropdownHidden;
        let showBtns = divEl.querySelectorAll("button");
        let resultBtns = this.toolbar.contents.filter((item, index) => {
            return index < k;
        });
        this.toolbarBtns = resultBtns ? [...resultBtns] : [];
        // for (let m = k; m < showBtns.length; m++) {
        //     this.renderer.setStyle(showBtns[m], 'display', 'none');
        // }
        if (this.toolbar.contents && this.toolbar.contents.length) {
            this.inMoreButtonContents = this.toolbar.contents.filter(
                (item, index) => {
                    return index >= k;
                }
            );
        }
    }
    /**
     * å¨æé®ç¶æååæ¶è§£é¤éå¶
     */
    private unBindResizeEvent() {
        if (this.ro) {
            if (this.resizeEl) {
                this.ro.unobserve(this.resizeEl);
            }
            this.ro = null;
        }
    }
    /**
     * @return string
     * '' ä¸å­å¨æé®ï¼æ æè°ä½ç½®
     * 'inHead' æé®å¨å¤´é¨
     * 'inContent' æé®å¨åå®¹ä½ç½®
     */
    private getToolbarPosition() {
        if (
            this.toolbar &&
            this.toolbar.hasOwnProperty("position") &&
            this.toolbar.hasOwnProperty("contents") &&
            this.toolbar.contents.length > 0
        ) {
            // å¦ææå±æ§ï¼ä¸æé®æå¼
            if (this.toolbar.position === "inHead") {
                return "inHead";
            } else {
                return "inContent";
            }
        }
        return "";
    }
    /**
     * å¤æ­æ¯å¦æ¾ç¤ºãéè
     * @param id
     * @param state
     */
    getToolbarState(
        id: string,
        state: Observable<any> = this.btnStates,
        defaultValue: boolean = false
    ) {
        if (state) {
            return state.pipe(
                map((n: any) => {
                    if (n[id] === undefined) {
                        // console.warn(`æªæ¾IDä¸ºï¼${id} ç¸å³ç¶æè®¾ç½®ï¼`);
                        return defaultValue;
                    }
                    return n[id];
                })
            );
        } else {
            return of(defaultValue);
        }
    }
    clickHandler(btnInfo) {
        if (!btnInfo) {
            return;
        }
        this.clickItems.next(btnInfo);
    }
}
